body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


button {
  background-color: #4CAF50;
  color: white;
  padding: 12px 20px;
  border: none;
  border-radius: 4px;
  float: right;
  font-size: large;
  cursor: pointer;
}

button.success {
  background-color: #4CAF50;
}

button.primary {
  background-color: blue;
}

button.fail {
  background-color: red;
}

button:disabled{
  background-color: rgb(172, 172, 172);
  cursor: no-drop;
}

.footer {
  padding: 20px;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  color: white;
  background: #32456a;
  text-align: center;
}

.footer a:link, a:visited { 
    color: white;
    text-decoration: none;
}

.footer a:link:active, a:visited:active { 
    color: white;
}


label span {
  color: red
}

.form {
  border-radius: 5px;
  padding: 20px;
}

fieldset p {
  padding: 5px;
}

label{
  margin: 0 0 5px 0;
  width: 100%;
}

input {
  width: 100%; 
  padding: 4px;
}

dialog {
  width: 75%;
  background-color: #fff;
  box-shadow: 0 0 0.625rem rgba(0, 0, 0, 0.2);
}

.modalDialogClose {
  float: right;
}

.modalOverlay {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  padding: 1rem;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 9999;
  opacity: 1;
  overflow-x: hidden;
  overflow-y: auto;
  -webkit-animation: show .5s ease;
          animation: show .5s ease;
}

.TodoItem {
    background: #32456a;
    padding: 5px;
    width: 200px;
    height: 150px;
    margin-top: 10px;
    margin-right: 10px;
    color: white;
    text-align: center;
    cursor: move;
    border: 2px solid #666666;
    border-radius: 10px;
    -ms-box-shadow: inset 0 0 3px #000;
    box-shadow: inset 0 0 3px #000;
}

.over {
  border: 2px #000;
}

.TodoItem.dragStart {
  opacity: 0.4;
}

.TodoItem span {
  float: right;
  margin-right: 5px;
}

span:hover {
  cursor: pointer;
}

.TodoItem .status {
  float: left;
  padding: 5px;
  background: #3677a1;
  border-radius: 25px;
  border: #013a5f 1px solid;
  font-weight: bolder;
}

.TodoItem .body {
  clear: both;
}

.TodoList-header {
  background: #32456a;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.TodoList-body {
  padding: 1%;
}

.TodoList-container {
  margin: auto;
  list-style: none;
  display: flex;
  flex-flow: row wrap;
  align-items: flex-start;
}
